import 'bootstrap/dist/css/bootstrap.css';
import '../styles/style.scss';

import JsonSchema from './schemas/jsonSchema';
import SCHEMA_LOGIN from './schemas/login.schema'
import { API_URL_BASE, API_URLS, UI_URLS } from './constants';
import { serializeObject } from './utils';


class LoginUI {

    constructor() {
        const $ = document.querySelector.bind(document);
        const $form = $("form");
        this.ui = {
            form: $form,
            inputs: {
                user: $form.querySelector("#input-user"),
                password: $form.querySelector("#input-password"),
            },
            buttons: {
                login: $form.querySelector("#btn-login"),
            },
            toast: $form.querySelector('.toast'),
            links:{
                forgotPass: $('#link-forgot-pass')
            }
        };

        this.ui.links.forgotPass.href = UI_URLS.FORGOT_PASS;
        this.addEventsToButtons();
    }

    clearInputErrors() {
        Object.values(this.ui.inputs).forEach(input => {
            input.classList.remove('is-invalid');
        });
    }

    validateCredentials(credentialsData) {
        console.info(credentialsData);
        const errorObj = JsonSchema.validateSchema(credentialsData, SCHEMA_LOGIN);
        this.clearInputErrors();
        errorObj.user && this.ui.inputs.user.classList.add('is-invalid');
        errorObj.password && this.ui.inputs.password.classList.add('is-invalid');

        return errorObj;
    }

    loginWithCredentials(credentials, callback){
        const url = `${API_URLS.VALIDATE_CREDENTIALS}`;

        fetch(url, {
            method: 'POST',
            headers: {'Content-Type': 'application/x-www-form-urlencoded'},
            body: serializeObject(credentials),
        })
        .then(res => res.json())
        .catch(error => console.error(`Error: ${error}`))
        .then(response => callback(response));
    }

    addEventsToButtons() {
        const thisClass = this;
        this.ui.buttons.login.addEventListener('click', (evt) => {
            evt.preventDefault();
            thisClass.hideErrors();
            const credentialsData = {
                user: this.ui.inputs.user.value,
                password: this.ui.inputs.password.value,
            };
            const validationErrors = thisClass.validateCredentials(credentialsData);
            if (!Object.keys(validationErrors).length) {

                this.ui.buttons.login.disabled = true;
                thisClass.loginWithCredentials(credentialsData, (response) => {
                    console.log(response);
                    this.ui.buttons.login.disabled = false;
                    if (response) {
                        if (response.ok) {
                            localStorage.user  = response.obj.usuario_login;
                            localStorage.email = response.obj.Usuario_correoPersonal;
                            localStorage.cel   = response.obj.usuario_telefono;

                            const redirectUrl = `${location.protocol}/${API_URL_BASE}${UI_URLS.VINCULATE_ACCOUNTS}`;
                            window.location.href = redirectUrl;
                        } else {
                            console.error(`Server Error: ${response.mensaje}`);
                            thisClass.showErrors();
                        }
                    } else  {
                        console.error('Unknow server error. No response')
                    }

                });
            }
        });
    }

    showErrors() {
        this.ui.toast.classList.add('visible');
    }

    hideErrors() {
        this.ui.toast.classList.remove('visible');
    }
}

document.addEventListener('DOMContentLoaded', () => {
    new LoginUI();
});
