/* tslint:disable:prefer-conditional-expression */
import { Validator } from 'jsonschema';

export default class JsonSchema {
    static removeEmpty(data) {
        const newData = { ...data };
        Object.keys(data).forEach((key) => {
            if (typeof (newData[key]) === 'object') {
                newData[key] = JsonSchema.removeEmpty(newData[key]);
            } else if (newData[key] === '') {
                delete newData[key];
            }
        });
        return newData;
    }

    static validateSchema(data, schema) {
        if (!schema) {
            throw new Error('Schema es requerido');
        }
        if (!data) {
            throw new Error('Data es requerida');
        }
        const newData = JsonSchema.removeEmpty(data);
        const validator = new Validator();
        const validationResult = validator.validate(newData, schema);
        if (validationResult.errors.length > 0) {
            const errors = JsonSchema.buildErrorObject(validationResult);
            return errors;
        }

        return {};
    }

    static buildErrorObject(validationResult) {
        const errorsResult = {};
        const formErrors = validationResult.errors || validationResult.errorDetail;
        formErrors.forEach((error) => {
            let names = [];
            let schemaProperties;
            switch (error.name) {
                case 'type':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors[error.name] : `Campo debe ser del tipo ${schemaProperties[error.name]}`;
                    break;
                case 'maximum':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors[error.name] : `Valor máximo es ${schemaProperties[error.name]}`;
                    break;
                case 'minimum':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors[error.name] : `Valor mínimo es ${schemaProperties[error.name]}`;
                    break;
                case 'required': {
                    schemaProperties = error.schema.properties;
                    names = error.property.split('.');
                    names.shift();
                    const instance = names.length > 0 ? (`${names.join('.')}.${error.argument}`) : error.argument;
                    const msgError = (schemaProperties[error.argument].errors && schemaProperties[error.argument].errors[error.name]) ? schemaProperties[error.argument].errors.required : 'Campo es requerido';
                    errorsResult[instance] = msgError;
                    break;
                }
                case 'pattern':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors.pattern : `Formato no válido con el patron "${schemaProperties.pattern}"`;
                    break;
                case 'minLength':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors.minLength : `Longitud mínima es ${schemaProperties.minLength}`;
                    break;
                case 'maxLength':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors.maxLength : `Longitud máxima es ${schemaProperties.minLength}`;
                    break;
                case 'enum':
                    schemaProperties = error.schema;
                    names = error.property.split('.');
                    names.shift();
                    errorsResult[names.join('.')] = (schemaProperties.errors && schemaProperties.errors[error.name]) ? schemaProperties.errors[error.name] : `No es un valor valido para ${schemaProperties.enum.join(', ')}`;
                    break;
                default:
                    errorsResult.schema = 'Tipo de error no definido';
            }
        });
        return errorsResult;
    }

    static humanizeError(msg) {
        let newMsg;

        switch (true) {
            case msg === 'does not meet minimum length of 1':
                newMsg = 'Campo es requerido';
                break;
            case (msg !== 'does not meet minimum length of 1' && msg.search('does not meet minimum length of') >= 0):
                newMsg = msg.replace('does not meet minimum length of', 'Debe tener como mínimo');
                break;
            case (msg.search('requires property') >= 0):
                newMsg = 'Campo es requerido';
                break;
            case (msg.search('does not meet maximum length of') >= 0):
                newMsg = `${msg.replace('does not meet maximum length of', 'Debe tener hasta')} números`;
                break;
            case (msg === 'does not match pattern "^[0-9]*$"'):
                newMsg = 'Solo se permiten valores numéricos';
                break;
            default:
                newMsg = msg;
        }
        return newMsg;
    }
}
