export function convertFormData(arrayData) {
    const objData = {};
    arrayData.forEach(element => {
        objData[element.name] = element.value;
    });

    return objData;
};

export function serializeObject(obj) {
    const array = Object.keys(obj).map(key => {
        return `${key}=${obj[key]}`;
    });
    return array.join('&');
}